<template>
  <div>
    <h5>Import</h5>
    <DocSectionCode :code="importCode" importCode />

    <h5>Getting Started</h5>
    <p>
      InputNumber is used with the standard v-model directive. Component always provides a number type although
      formatting on the input is a string.
    </p>
    <DocSectionCode :code="baseCode" />

    <h5>Decimal Mode</h5>
    <p>
      Format is defined using the <i>mode</i> property, "decimal" is the default value allowing only integers when there
      is no other configuration.
    </p>

    <DocSectionCode :code="decimalModeCode" />

    <p>
      Fractions are configured with the <i>minFractionDigits</i> property. Optionally <i>maxFractionDigits</i> can be
      used to defined a boundary for the maximum digits.
    </p>
    <DocSectionCode :code="decimalModeCode2" />

    <p>
      <i>locale</i> option is available to set the localization information such as grouping and decimal symbols where
      default value is the browser locale. Locales are defined per
      <a href="https://tools.ietf.org/html/rfc5646">BCP Language Tag</a>.
    </p>
    <DocSectionCode :code="decimalModeCode3" />

    <h5>Currency</h5>
    <p>
      Currency formatting is specified by setting the <i>mode</i> option to currency and <i>currency</i> property. In
      addition <i>currencyDisplay</i> option allows how the currency is displayed, valid values are "symbol" (default)
      or "code".
    </p>
    <DocSectionCode :code="currencyCode" />

    <h5>Prefix and Suffix</h5>
    <p>
      Custom texts e.g. units can be placed before or after the input section with the <i>prefix</i> and
      <i>suffix</i> properties.
    </p>
    <DocSectionCode :code="prefixAndSuffixCode" />

    <h5>Buttons</h5>
    <p>
      Spinner buttons is enabled using the <i>showButtons</i> options and layout is defined with the
      <i>buttonLayout</i>. Default value is "stacked" whereas "horizontal" and "stacked" are alternatives. Note that
      even there are no buttons, up and down arrow keys can be used to spin the values with keyboard.
    </p>
    <DocSectionCode :code="buttonsCode" />

    <h5>Step</h5>
    <p>Step factor is 1 by default and can be customized with <i>step</i> option.</p>
    <DocSectionCode :code="stepCode" />

    <h5>Min and Max Boundaries</h5>
    <p>Value to be entered can be restricted by configuring the <i>min</i> and <i>max</i> options.</p>
    <DocSectionCode :code="minAndMaxBoundariesCode" />

    <h5>Properties</h5>
    <p>InputNumber passes any valid attribute to the underlying input element.</p>
    <div class="doc-tablewrapper">
      <table class="doc-table">
        <thead>
          <tr>
            <th>Name</th>
            <th>Type</th>
            <th>Default</th>
            <th>Description</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>value</td>
            <td>number</td>
            <td>null</td>
            <td>Value of the component.</td>
          </tr>
          <tr>
            <td>format</td>
            <td>boolean</td>
            <td>true</td>
            <td>Whether to format the value.</td>
          </tr>
          <tr>
            <td>showButtons</td>
            <td>boolean</td>
            <td>false</td>
            <td>Displays spinner buttons.</td>
          </tr>
          <tr>
            <td>buttonLayout</td>
            <td>string</td>
            <td>stacked</td>
            <td>Layout of the buttons, valid values are "stacked" (default), "horizontal" and "vertical".</td>
          </tr>
          <tr>
            <td>incrementButtonClass</td>
            <td>string</td>
            <td>null</td>
            <td>Style class of the increment button.</td>
          </tr>
          <tr>
            <td>decrementButtonClass</td>
            <td>string</td>
            <td>null</td>
            <td>Style class of the decrement button.</td>
          </tr>
          <tr>
            <td>incrementButtonIcon</td>
            <td>string</td>
            <td>pi pi-angle-up</td>
            <td>Style class of the increment button.</td>
          </tr>
          <tr>
            <td>decrementButtonIcon</td>
            <td>string</td>
            <td>pi pi-angle-down</td>
            <td>Style class of the decrement button.</td>
          </tr>
          <tr>
            <td>locale</td>
            <td>string</td>
            <td>null</td>
            <td>Locale to be used in formatting.</td>
          </tr>
          <tr>
            <td>localeMatcher</td>
            <td>string</td>
            <td>best fit</td>
            <td>
              The locale matching algorithm to use. Possible values are "lookup" and "best fit"; the default is "best
              fit". See
              <a
                href="https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl#Locale_negotiation"
                >Locale Negotation</a
              >
              for details.
            </td>
          </tr>
          <tr>
            <td>mode</td>
            <td>string</td>
            <td>decimal</td>
            <td>Defines the behavior of the component, valid values are "decimal" and "currency".</td>
          </tr>
          <tr>
            <td>prefix</td>
            <td>string</td>
            <td>null</td>
            <td>Text to display before the value.</td>
          </tr>
          <tr>
            <td>suffix</td>
            <td>string</td>
            <td>decimal</td>
            <td>Text to display after the value.</td>
          </tr>
          <tr>
            <td>currency</td>
            <td>string</td>
            <td>null</td>
            <td>
              The currency to use in currency formatting. Possible values are the
              <a href="https://www.currency-iso.org/en/home/tables/table-a1.html">ISO 4217 currency codes</a>, such as
              "USD" for the US dollar, "EUR" for the euro, or "CNY" for the Chinese RMB. There is no default value; if
              the style is "currency", the currency property must be provided.
            </td>
          </tr>
          <tr>
            <td>currencyDisplay</td>
            <td>string</td>
            <td>symbol</td>
            <td>
              How to display the currency in currency formatting. Possible values are "symbol" to use a localized
              currency symbol such as €, "code" to use the ISO currency code, "name" to use a localized currency name
              such as "dollar"; the default is "symbol".
            </td>
          </tr>
          <tr>
            <td>useGrouping</td>
            <td>boolean</td>
            <td>true</td>
            <td>Whether to use grouping separators, such as thousands separators or thousand/lakh/crore separators.</td>
          </tr>
          <tr>
            <td>minFractionDigits</td>
            <td>number</td>
            <td>null</td>
            <td>
              The minimum number of fraction digits to use. Possible values are from 0 to 20; the default for plain
              number and percent formatting is 0; the default for currency formatting is the number of minor unit digits
              provided by the
              <a href="https://www.currency-iso.org/en/home/tables/table-a1.html">ISO 4217 currency code list</a> (2 if
              the list doesn't provide that information).
            </td>
          </tr>
          <tr>
            <td>maxFractionDigits</td>
            <td>number</td>
            <td>null</td>
            <td>
              The maximum number of fraction digits to use. Possible values are from 0 to 20; the default for plain
              number formatting is the larger of minimumFractionDigits and 3; the default for currency formatting is the
              larger of minimumFractionDigits and the number of minor unit digits provided by the
              <a href="https://www.currency-iso.org/en/home/tables/table-a1.html">ISO 4217 currency code list</a> (2 if
              the list doesn't provide that information).
            </td>
          </tr>
          <tr>
            <td>min</td>
            <td>number</td>
            <td>null</td>
            <td>Mininum boundary value.</td>
          </tr>
          <tr>
            <td>max</td>
            <td>number</td>
            <td>null</td>
            <td>Maximum boundary value.</td>
          </tr>
          <tr>
            <td>step</td>
            <td>number</td>
            <td>1</td>
            <td>Step factor to increment/decrement the value.</td>
          </tr>
          <tr>
            <td>inputStyle</td>
            <td>any</td>
            <td>null</td>
            <td>Inline style of the input field.</td>
          </tr>
          <tr>
            <td>inputClass</td>
            <td>string</td>
            <td>null</td>
            <td>Style class of the input field.</td>
          </tr>
        </tbody>
      </table>
    </div>

    <h5>Events</h5>
    <p>Any valid event such as focus, blur and input are passed to the underlying input element.</p>

